
import { defineComponent } from "vue";
import DOMUtils from "@/utils/DOMUtils";

export default defineComponent({
  name: "HamburgerMenu",
  props: {
    links: {
      type: Map,
      required: true,
    },
  },
  data: function () {
    return {
      menuOpen: false,
    };
  },
  methods: {
    scroll(to: string) {
      DOMUtils.smoothScroll(to);
      this.toggle();
    },
    toggle() {
      this.menuOpen = !this.menuOpen;
    },
  },
});
